html, body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.App {
  width: 360px;
  height: 616px;
  margin-top: 24px;
  background-color: #f5f5f5;
}

.nav {
  height: 56px;
  background: #f7fafc;
  border-bottom: 2px solid #e6e8ea;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

#search input{
  height: 55px;
  border: none;
  background: none;
  margin-left: 15px;
  width: 225px;
  font-size: 14pt;
}

#controls {
  height: 55px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

#controls .nav-button {
  width: 48px;
  height: 100%;
  display: block;
}

#controls .nav-button:active {
  background-color: #e5e5e5;
}

::-webkit-input-placeholder {
  font-size: 20pt;
  color: #b2b2b2;
  font-weight: 500;
}

::-moz-placeholder {
  font-size: 14pt;
  color: #666;
  font-weight: normal;
}



